<template>
  <div class="home">
    <h1 class="title">skultes <br><span>gym</span></h1>
    <LoadingSpinner v-if="this.loading" />
    <div v-else>
      <button @click="scrollDown" class="btn-rounded-clear">Pieteikties &#x203A;</button>
      <InputForm :appointments="this.appointments"/>
      <Home :appointments="this.appointments" />
    </div>
    <span ref="bottom"></span>
  </div>
</template>

<script>
import Home from '@/components/Home.vue'
import LoadingSpinner from '@/components/LoadingSpinner.vue'
import InputForm from '@/components/InputForm.vue'
import db from '../components/firebaseInit'
import { collection, query, where, getDocs, orderBy } from 'firebase/firestore'

export default {
  name: 'HomeView',
  components: {
    Home, InputForm, LoadingSpinner
  }, 
  data(){
    return{
      appointments: [],
      loading: true
    }
  },
  mounted(){
    console.log('bang')
    this.loadAppointments()
  },
  methods:{
    scrollDown(){
      const el = this.$refs.bottom

      if(el){
        el.scrollIntoView({ behavior: "smooth"})
      }
      else{
        console.log('error')
      }
    },
    async loadAppointments(start=this.startingDate){
      this.loading = true
      const q = query(this.appointmentsCollection, where("date", ">=", start), orderBy('date'), orderBy('start_index'))
      const appointmentsSnapshot = await getDocs(q);
      this.appointments = []
      appointmentsSnapshot.forEach(doc => {
        this.appointments.push(doc.data())
      });
      this.loading = false
    },
  },
  computed:{
    startingDate(){
      const today = new Date();
      const currentDate = new Date(today);
      currentDate.setDate(today.getDate());
      const dateValue = currentDate.toLocaleDateString('en-US', { year: 'numeric', month: '2-digit', day: '2-digit' });
      const [month, day, year] = dateValue.split('/');
      const dateVal = parseInt(year+month+day)
      return dateVal
    },
    appointmentsCollection(){
      return collection(db, 'appointments')
    }
  },
}
</script>

